/*jshint esversion: 6 */
import React, { Component } from "react";
import "isomorphic-unfetch";
import * as Styled from "./styled/Results.js";
import { getAPIHostname } from "lib/getAPI";
import Query from "./Query.js";
/* redux */
import { connect } from "react-redux";
import * as actions from "data/actions";

/* 
	Component 
*/
class Results extends Component {
	constructor() {
		super();
		this.state = {
			jobs: []
		};
	}
	async componentWillMount() {
		const res = await fetch(getAPIHostname() + `/api/v1/jobs?text=&start=0&limit=10000`);
		const json = await res.json();
		// this.setState({ jobs: json.data || [], jobs_count: json.data.length || 0 });
		this.props.jobsAdd(json.data || []);
	}

	filterJobs(jobs) {
		jobs = jobs.map(function(item) {
			item.status = item.status || "new";
			return item;
		});
		return jobs;
	}
	render() {
		var jobs = this.props.jobs;
		jobs = this.filterJobs(jobs);
		// get on with it...
		var Jobs = [];
		if (jobs) {
			jobs.forEach(function(job, i) {
				Jobs.push(
					<div key={job._id}>
						<div>{job.text}</div>
						<sup>{job.location}</sup>
					</div>
				);
			});
		}
		return (
			<Styled.Results>
				<h2>Query:</h2>
				<Query />
				<h2> </h2>

				<h2>Results: {jobs.length}</h2>
				<div>{Jobs}</div>
			</Styled.Results>
		);
	}
}
const mapStateToProps = (state, ownProps) => ({
	jobs: state.jobs
});
const mapDispatchToProps = (dispatch, ownProps) => ({
	jobsAdd: data => {
		dispatch(actions.jobsAdd(data));
	}
});
const ConnectedResults = connect(mapStateToProps, mapDispatchToProps)(Results);

/*
	Components
*/
export default ConnectedResults;



